@mixin object-fit($fit: none, $position: null) {
  -o-object-fit: $fit;
  object-fit: $fit;
  @if $position {
    -o-object-position: $position;
    object-position: $position;
    font-family: 'object-fit: ' + $fit +' object-position: ' + $position + ''; }
  @else {
    font-family: 'object-fit: ' + $fit; } }

@mixin flex($align: center, $justify: center, $direction: row) {
  display: flex;
  flex-direction: $direction;
  align-items: $align;
  justify-content: $justify; }

@mixin general-block($width: 100%, $height: 100%) {
  display: block;
  height: $height;
  width: $width; }

@mixin triangle($color, $borders, $height: (($borders * 2) * 0.866%)) {
  width: 0;
  height: 0;
  border-top: $borders solid transparent;
  border-left: $height solid $color;
  border-bottom: $borders solid transparent; }

@mixin centerAbsolute() {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0); }

@mixin placeholder() {
  &::-webkit-input-placeholder  /* Chrome/Opera/Safari */ {
    @content; }

  &::-moz-placeholder  /* Firefox 19+ */ {
    @content; }

  &:-ms-input-placeholder  /* IE 10+ */ {
    @content; }

  &:-moz-placeholder  /* Firefox 18- */ {
    @content; } }

@mixin font-face ($font-name, $file-name, $font-weight) {
  @font-face {
    font-family: quote($font-name);
    src: url("../fonts/"+ $file-name + ".woff2") format("woff2"),
    url("../fonts/"+ $file-name + ".woff") format("woff") {}
    font-weight: $font-weight;
    font-style: normal; } }

@mixin fully {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

@mixin transition {
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.6, 0, 0.41, 1); }

@mixin transition05 {
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.6, 0, 0.41, 1); }

@mixin transition1 {
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.6, 0, 0.41, 1); }

@mixin transition15 {
  transition-duration: 1.5s;
  transition-timing-function: cubic-bezier(0.6, 0, 0.41, 1); }

@function letter-spacing($size, $font-size) {
  @return ($size / $font-size) * 1em; }
