body {
  width: 100%;
  height: 100vh;
  background-image: url("../img/bg-1.jpg");
  background-repeat: no-repeat;
  background-size: cover; }

.header {
  .test {
    width: 100%;
    background-color: #394757;
    height: 13px;
    @include res('tab') {
      width: 100%; } } }
.header__wrap {
  position: relative; }
.header__contact {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  @include res('mob') {
    display: none; } }
.header__logo {
  padding-top: 20px;
  margin-left: 30px;
  @include res('tab') {
    width: 15%;
    margin-right: 30px; }
  @include res('mob') {
    width: 20%; } }
.header__logo-img {
  width: 100%; }
.header__contact-position-one {
  display: flex;
  align-items: center;
  padding-top: 20px;
  font-family: "OpenSans";
  font-weight: 400;
  @include res('tab') {
    font-size: 12px;
    padding-top: 0;
    margin-right: 20px; }
  a {
    text-decoration: none;
    color: #000; }
  img {
    padding-right: 10px; } }

.header__contact-position {
  padding-top: 50px;
  font-family: "OpenSans";
  font-weight: 400;
  a {
    text-decoration: none;
    color: #000; }
  @include res('tab') {
    padding-top: 30px;
    font-size: 12px;
    margin-right: 30px; } }
.img-rounded {
  padding-left: 15px;
  &:last-child {} }
.header__nav {
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  a {
    color: #303030;
    text-decoration: none;
    font-size: 16px;
    text-transform: uppercase;
    font-family: "OpenSans";
    font-weight: bold;
    &:hover {
      cursor: pointer;
      color: #7b9bc1;
      transition: .3s; } }
  @include res('tab') {
    a {
      font-size: 13px;
      font-weight: 400; } }
  @include res('mob') {
      display: none; } }

.background-nav {
  background-color: #e7edf4;
  width: 100%; }
.call-back {
  height: 50px;
  width: 226px;
  background-color: #b1c4da;
  display: flex;
  justify-content: center;
  align-items: center;
  a {
    color: #fff;
    &:hover {
      cursor: pointer;
      color: #000;
      transition: .3s; } }
  @include res('tab') {
    margin-right: -15px;
    width: 175px;
    height: 50px; } }

.header__nav-link {
  &:last-child {}
  @include res('tab') {
    &:last-child {
      margin-right: 0px; } } }

.menu {
  @include res('mob') {
  	display: flex;
  	&__input {
  		display: none;
  		&:not(checked) ~ .menu__content {
  			display: none; }
  		&:checked ~ .menu__content {
  			display: block; } }
  	&__label {
  		cursor: pointer; } } }
.menu__content {
  a {
    font-size: 16px;
    font-family: "OpenSans";
    font-weight: 400;
    color: #000;
    text-decoration: none;
    line-height: 1.5;
    &:hover {
      cursor: pointer;
      color: #7b9bc1; } } }
.menu__label {
  height: 32px;
  width: 32px; }
.menu {
  display: none;
  @include res('mob') {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #e7edf4; } }
.menu__content-edit {
  display: flex;
  flex-direction: column;
  align-items: center; }
